<template>
      <div style="padding:10px 20px">
         <el-row style="color:gray">
            <el-col :span="8" style="text-align:center;">
               <el-divider style="color:#66B1FF;">属性编辑工具</el-divider>
            </el-col>
            <el-col :span="5" style="text-align:center;padding-left: 10px;">
               <el-divider style="color:#66B1FF;">钻孔专用工具</el-divider>
            </el-col>
            <el-col :span="11">&nbsp;</el-col>
         </el-row>
         <el-row style="color:gray;padding:10px 0px 10px 10px">
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;">
               <div style="width: 100%; text-align: center;">
                  <div>
                     <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-edit-outline"></i>
                  </div>
                  <div style="cursor: pointer;color: #66B1FF;font-size:12px;">
                     编辑
                  </div>
               </div>
            </el-col>
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;">
               <div style="width: 100%; text-align: center;">
               <div>
                  <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-download"></i>
               </div>
               <div style="cursor: pointer;color: #66B1FF;font-size:12px">
                  保存
               </div>
               </div>
            </el-col>
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;">
               <div style="width: 100%; text-align: center;">
               <div>
                  <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-refresh-left"></i>
               </div>
               <div style="cursor: pointer;color: #66B1FF;font-size:12px">
                  撤销
               </div>
               </div>
            </el-col>
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;">
               <div style="width: 100%; text-align: center;" @click="handleDelet">
                  <div>
                     <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-delete"></i>
                  </div>
                  <div style="cursor: pointer;color: #66B1FF;font-size:12px">
                     删除
                  </div>
               </div>
            </el-col>
            <el-col :span="4" style="text-align:center;padding-right: 15px;border-right: 5px solid #66B1FF;">
               <el-input
                  placeholder="请输入内容"
                  suffix-icon="el-icon-search"
                  v-model="searchText"
                  >
               </el-input>
            </el-col>
            
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;margin-left:15px">
               <div style="width: 100%; text-align: center;" @click="previewVisible1 = true">
                  <div>
                     <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-s-grid"></i>
                  </div>
                  <div style="cursor: pointer;color: #66B1FF;font-size:12px">
                     坐标计算
                  </div>
               </div>
            </el-col>
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;margin-left:15px">
               <div style="width: 100%; text-align: center;" @click="previewVisible2 = true">
               <div>
                  <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-film"></i>
               </div>
               <div style="cursor: pointer;color: #66B1FF;font-size:12px">
                  埋深计算
               </div>
               </div>
            </el-col>
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;margin-left:15px">
               <div style="width: 100%; text-align: center;" @click="previewVisible3 = true">
               <div>
                  <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-s-operation"></i>
               </div>
               <div style="cursor: pointer;color: #66B1FF;font-size:12px">
                  地层配置
               </div>
               </div>
            </el-col>
            <el-col :span="1" style="display: flex;justify-content: center;align-items: center;margin-left:15px">
               <div style="width: 100%; text-align: center;" @click="previewVisible4 = true">
               <div>
                  <i style="cursor: pointer; color: gray;font-size:30px" class="el-icon-document-checked"></i>
               </div>
               <div style="cursor: pointer;color: #66B1FF;font-size:12px">
                  数据检查
               </div>
               </div>
            </el-col>
            <el-col :span="4">&nbsp;</el-col>
            <el-col :span="5" >
               <el-select v-model="targetTable" placeholder="请选择表" @change="selectTargetTable">
                  <el-option v-for="item in tableData" :key="item.tableNamec" :label="item.tableNamec" :value="item.tableNamec" ></el-option> 
               </el-select>
            </el-col>
            <el-col :span="1" style="text-align: right;">
               <el-button @click="mapShow">地图显示</el-button>
            </el-col>
         </el-row>
         <div style="display:flex">
            <div style="flex:6;overflow-x: scroll;" >
               <!-- <ux-grid border
               show-overflow
               ref="plxTable"
               keep-source
               height="600"
               :highlightCurrentRow="false"  
               :edit-config="{trigger: 'click', mode: 'cell'}">
                  <ux-table-column type="checkbox" width="60"></ux-table-column>
                  <ux-table-column v-for="(item,index) in currentTableFields" :key="index" :field="item.fieldNamec" :title="item.fieldName" min-width="140" edit-render>
                     <template v-slot:edit="scope">
                        <el-input v-model="scope.row.item"></el-input>
                     </template>
                  </ux-table-column>
                  <ux-table-column title="操作" width="120">
                     <template v-slot="{ row }">
                        <el-button @click="saveEvent(row)">保存</el-button>
                     </template>
                  </ux-table-column>
               </ux-grid> -->
               <ux-grid border
                  show-overflow
                  ref="plxTable"
                  keep-source
                  height="600"
                  :data="mytableData.filter(data => !searchText || Object.values(data).some((value) => String(value).toLowerCase().includes(searchText.toLowerCase())))"
                  :highlightCurrentRow="false"
                  show-summary
                  :edit-config="{trigger: 'manual', mode: 'row'}">
                  <ux-table-column type="checkbox" width="60"></ux-table-column>
                  <ux-table-column v-for="(item,index) in currentTableFields" :key="item.fieldName" :field="item.fieldName" :title="item.fieldNamec"  width="160" edit-render>
                     <template v-slot:edit="scope">
                        <el-input v-model="scope.row[item.fieldName]"></el-input>
                     </template>
                  </ux-table-column>
                  
                     <ux-table-column title="操作" width="200">
                     <template v-slot="{ row }">
                              <!--判断行是否激活了, 编辑按钮执行的方式就是为了去激活-->
                              <template v-if="$refs.plxTable.isActiveByRow(row)">
                                 <el-button @click="saveEvent(row)" type="primary">保存</el-button>
                                 <el-button @click="cancelRowEvent(row)">取消</el-button>
                           </template>
                           <template v-else>
                                 <el-button @click="editRowEvent(row)">编辑</el-button>
                              </template>
                     </template>
                     </ux-table-column>
               </ux-grid>
            </div>
            <div style="flex:5" v-if="showMap">
               <div style="height:600px;width: 100%;background-color: #66B1FF;" id="olMap"></div>
            </div>
         </div>
         <!-- <div style="height:600px;width: 100%;" id="olMap"></div> -->
               
         
         <div style="width:100%;padding: 20px;display: flex;">
            <el-button @click="back()">上一步</el-button>
            <el-button type="primary" @click="getTableData()">确认</el-button>
         </div>
         <!-- 操作弹窗 -->
         <!--1. 坐标计算 -->
         <el-dialog :modal-append-to-body=false title="" width="35%" :visible.sync="previewVisible1">
               <div slot="title" style="color:black;width: 100%;border-bottom: 1px solid #DCDFE6;height: 30px;">
                  <i class="el-icon-s-grid"></i>
                  <span style="margin-left:5px"> 坐标计算</span>
               </div>
               <el-form :inline="true" :model="coordinateForm" class="demo-form-inline" style="margin:auto" size="mini">
                  <div style="width:80%;margin:0 auto">
                     <el-form-item label="线位数据库:">
                        <el-input v-model="coordinateForm.dbName" placeholder="请输入内容" style="width:100%"></el-input>
                     </el-form-item>
                     <el-form-item>
                        <el-upload
                           :show-file-list="false"
                           class="upload-demo"
                           action="#"
                           :auto-upload="false"
                           :on-change="handleChange"
                           :file-list="coordinateForm.fileList">
                           <el-button type="primary" size="mini">选择文件</el-button>
                           </el-upload>
                     </el-form-item>
                  </div>
                  <div style="width:100%;padding: 30px;">
                     <div style="border: 1px solid #DCDFE6;border-radius: 10px;height: 100%;position: relative;padding:50px 30px">
                        <h5 style="position:absolute;top:-10px;left:20px;font-size:14px;background-color:white">坐标转化参数</h5>
                           <div style="margin-left: 40px;">
                              <el-form-item label="中央经线:">
                                 <div style="display:flex">
                                    <el-input v-model="coordinateForm.lineD" style="width:50px;"></el-input>
                                    <div style="width:15px;padding-left: 5px;">°</div>
                                    <el-input v-model="coordinateForm.lineM" style="width:50px"></el-input>
                                    <div style="width:15px;padding-left: 5px;">'</div>
                                    <el-input v-model="coordinateForm.lineS" style="width:50px"></el-input>
                                    <div style="width:15px;padding-left: 5px;">"</div>
                                 </div>
                              </el-form-item>
                              <el-form-item label="投影高:">
                                 <el-input v-model="coordinateForm.reflectH" placeholder="请输入内容" style="width:80%;"></el-input><span style="margin-left:9px">m</span>
                              </el-form-item>
                           </div>
                     </div>
                  </div>
               </el-form>
               <span slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="previewVisible1 = false" size="small">开始转换</el-button>
                  <el-button @click="previewVisible1 = false" size="small">取 消</el-button>
               </span>
         </el-dialog>
         <!-- 2.埋深计算 -->
         <el-dialog :modal-append-to-body=false title="" width="30%" :visible.sync="previewVisible2">
               <div slot="title" style="color:black;width: 100%;border-bottom: 1px solid #DCDFE6;height: 30px;">
                  <i class="el-icon-film"></i>
                  <span style="margin-left:5px"> 埋深计算</span>
               </div>
               <span slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="previewVisible2 = false" size="small">开始转换</el-button>
                  <el-button @click="previewVisible2 = false" size="small">取 消</el-button>
               </span>
         </el-dialog>
         <!-- 3.地层配置 -->
         <el-dialog :modal-append-to-body=false title="" width="50%" :visible.sync="previewVisible3">
               <div slot="title" style="color:black;width: 100%;border-bottom: 1px solid #DCDFE6;height: 30px;">
                  <i class="el-icon-s-operation"></i>
                  <span style="margin-left:5px"> 地层配置</span>
               </div>
               <el-form :inline="true" :model="gconfigForm" class="demo-form-inline" style="margin:auto" size="mini">
                  <div style="width:100%;margin:0 auto;padding-left: 40px;">
                     <el-form-item label="标准地层版本:">
                        <el-select v-model="gconfigForm.groundType" placeholder="请选择活动区域" style="width:400px">
                           <el-option label="区域一" value="shanghai"></el-option>
                           <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                     </el-form-item>
                  </div>
                  <div style="width:100%;padding: 30px;">
                     <div style="border: 1px solid #DCDFE6;border-radius: 10px;height: 100%;position: relative;padding:30px 10px">
                        <h5 style="position:absolute;top:-10px;left:20px;font-size:14px;background-color:white">详细信息</h5>
                           <div style="margin-left: 40px;">
                              
                                 <el-form-item label="版本号:">
                                    <el-input v-model="gconfigForm.type" placeholder="请输入内容" style="width:100%;"></el-input>
                                 </el-form-item>
                                 <el-form-item label="版本名称:">
                                    <el-input v-model="gconfigForm.typeName" placeholder="请输入内容" style="width:100%;"></el-input>
                                 </el-form-item>
                                    <el-form-item label="项目名称:">
                                       <el-input v-model="gconfigForm.demoName" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                    <el-form-item label="测段名称:">
                                       <el-input v-model="gconfigForm.lengthName" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                    <el-form-item label="工点名称:">
                                       <el-input v-model="gconfigForm.dotName" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                    <el-form-item label="冠词代号:">
                                       <el-input v-model="gconfigForm.articleCode" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                    <el-form-item label="起始里程:">
                                       <el-input v-model="gconfigForm.startRoad" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                    <el-form-item label="终止里程:">
                                       <el-input v-model="gconfigForm.endRoad" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                    <el-form-item label="创建者:">
                                       <el-input v-model="gconfigForm.creater" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                    <el-form-item label="创建名称:">
                                       <el-input v-model="gconfigForm.creatName" placeholder="请输入内容" style="width:100%;"></el-input>
                                    </el-form-item>
                                 <el-form-item label="备注:">
                                    <el-input v-model="gconfigForm.desc" placeholder="请输入内容" style="width:100%;"></el-input>
                                 </el-form-item>
                              
                           </div>
                     </div>
                  </div>
               </el-form>
               <span slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="previewVisible3 = false" size="small">开始匹配</el-button>
                  <el-button @click="previewVisible3 = false" size="small">取 消</el-button>
               </span>
         </el-dialog>
         <!-- 4.数据检查 -->
         <el-dialog :modal-append-to-body=false title="" width="60%" :visible.sync="previewVisible4">
               <div slot="title" style="color:black;width: 100%;border-bottom: 1px solid #DCDFE6;height: 30px;">
                  <i class="el-icon-document-checked"></i>
                  <span style="margin-left:5px"> 数据检查</span>
               </div>
               <div style="display:flex;">
                  <div style="flex:5;padding:5px">
                     <div style="border: 1px solid #DCDFE6;border-radius: 10px;height: 100%;position: relative;padding:50px 30px;">
                        <h5 style="position:absolute;top:-10px;left:20px;font-size:14px;background-color:white">检查项</h5>
                           <div style="margin-left: 20px;display: inline-block;">
                              <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">钻孔信息检查</el-checkbox>
                              <div style="margin: 15px 0;"></div>
                                 <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                                    <ul>
                                       <li v-for="city in cities"><el-checkbox  :label="city" :key="city">{{city}}</el-checkbox></li>
                                    </ul>
                                 </el-checkbox-group>
                           </div>
                           <div style="margin-left: 30px;display: inline-block;">
                              <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">钻孔分层信息检查</el-checkbox>
                              <div style="margin: 15px 0;"></div>
                                 <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                                    <ul>
                                       <li v-for="city in cities"><el-checkbox  :label="city" :key="city">{{city}}</el-checkbox></li>
                                    </ul>
                                 </el-checkbox-group>
                           </div>
                     </div>  
                  </div>
                  <div style="flex:3;padding:5px">
                     <div style="border: 1px solid #DCDFE6;border-radius: 10px;height: 100%;position: relative;padding:50px 30px">
                        <h5 style="position:absolute;top:-10px;left:20px;font-size:14px;background-color:white">检查结果</h5>
                           <div style="margin-left: 40px;">
                              
                           </div>
                     </div>
                  </div>
               </div>
               <div slot="footer" class="dialog-footer" style="margin-top:20px;height: 50px;display:flex">
                  <div style="flex:5">
                     <el-button type="primary" @click="previewVisible4 = false" size="small">开始检查</el-button>
                     <el-button @click="previewVisible4 = false" size="small">取 消</el-button>
                  </div>
                  <div style="flex:2">
                     <el-button @click="previewVisible4 = false" size="small">下载详细信息</el-button>
                  </div>
               </div>
         </el-dialog>
      </div>     
</template>

<script>
import "ol/ol.css";
import { Tile as TileLayer } from "ol/layer";
import XYZ from "ol/source/XYZ";
import { defaults as defaultControls } from "ol/control";
import Map from "ol/Map.js";
import View from "ol/View.js";

import { mapState } from 'vuex';

// let map = null;
   const cityOptions = ['aaaaaaaaaa', 'bbbbbbbbbbb', 'aaaaaaaaa', 'ccccccccccc'];
    export default {
       computed:{
            ...mapState('dataparse',['tableData','fileType']),
         },
       data () {
           return {
            map:null,  //地图容器
            showMap:false,  //地图显示
            targetTable:'',  //选择当前目标表
            //右4操作框
            previewVisible1:false,
            previewVisible2:false,
            previewVisible3:false,
            previewVisible4:false,
            
            currentTableFields:[],
            history: [], // 存储操作历史记录
             
            //搜索
            searchText:'',  
            filteredData: [],
            //坐标计算
            coordinateForm: {
               fileList:[],
               dbName:'',
               lineD:'',
               lineM:'',
               lineS:'',
               reflectH:'',
            },
            //埋深计算
            depthForm:{
               user:'',
            },
            //地层配置
            gconfigForm:{
               groundType: '',
               type:'',
               typeName:'',
               demoName:'',
               lengthName:'',
               dotName:'',
               articleCode:'',
               startRoad:'',
               endRoad:'',
               creater:'',
               createName:'',
               desc:'',
            },
            //数据检查
            checkAll: false,
            checkedCities: [],
            cities: cityOptions,
            isIndeterminate: true,

            mytableData:[],
           }
         },

       mounted () {
         this.initMap();
        // 延迟加载
       },
       
       methods: {
         //右1上传
         handleChange(file){
            this.coordinateForm.fileList.push(file.raw);
            this.coordinateForm.dbName = file.name.substring(0, file.name.lastIndexOf('.')).replace(/\./g, '');;
            console.log("1",this.coordinateForm.fileList,"2",file);
         },
         selectTargetTable(value){
            const table =  this.tableData.find(item => item.tableNamec === value);
            this.currentTableFields = table.fieldRow;
            console.log("当前表的全部字段",this.currentTableFields);
            console.log("打印出values,fields",table.valueRows,table.fieldRow);
            this.mytableData = table.valueRows;
            // const values = table.valueRows;
            // const fields = table.fieldRow; 
            // const newArray = []; 
            // for (let i = 0; i < values.length; i++) {
            //    const row = values[i]; 
            //    const newObj = {}; 
            //    for (let j = 0; j < fields.length; j++) {
            //       const key = fields[j].fieldNamec;
            //       const value = row[j];
            //       newObj[key] = value;
            //    }
            //    newArray.push(newObj); 
            // }
            // console.log(newArray); 
            // this.mytableData = newArray;
            //最后给表格来reload
            this.$refs.plxTable.reloadData(table.valueRows)
            // 获取表格实例，里面可以得到列，表格数据等等
            console.log("获取表格实例",this.$refs.plxTable.tableExample())

            
         },
         mapShow() {
            // this.initMap();
            this.showMap = !this.showMap
         },
         initMap() {
            this.map = new Map({
            target: "olMap",
            view: new View({
               center: [114.5, 30.5], //中心点经纬度
               zoom: 5, //图层缩放大小
               projection: "EPSG:4326",
            }),
            controls: defaultControls({
               zoom: true,
               attribution: false,
               rotate: false,
            }),
            });

            // 添加地图
            let url = "http://t{0-7}.tianditu.com/DataServer?x={x}&y={y}&l={z}";
            url = `${url}&T=vec_c&tk=7a574f33b90173055cef00fca14a889a`;
            const source = new XYZ({
            url: url,
            projection: "EPSG:4326",
            });
            const tdtLayer = new TileLayer({
            source: source,
            });
            this.map.addLayer(tdtLayer);
            // 添加注记
            url = "http://t{0-7}.tianditu.com/DataServer?x={x}&y={y}&l={z}";
            url = `${url}&T=cva_c&tk=7a574f33b90173055cef00fca14a889a`;
            const sourceCVA = new XYZ({
            url: url,
            projection: "EPSG:4326",
            });
            const tdtcvaLayer = new TileLayer({
            source: sourceCVA,
            });
            this.map.addLayer(tdtcvaLayer);
            // this.addMyLayer(map);
         },
         //回退
         // undoChanges() {
         //       if (this.history.length > 0) {
         //       // 撤销更改，恢复到上一步操作的数据状态
         //       const snapshot = this.history.pop();
         //       this.mytableData = JSON.parse(JSON.stringify(snapshot));
         //    }
         // },
          // 点击保存
          saveEvent(row) {
            // 判断是否发生改变
            console.log(this.$refs.plxTable.isUpdateByRow(row))
            // 你也可以不使用这个判断，我是给示例，没做个性化需求
            if (this.$refs.plxTable.isUpdateByRow(row)) {
                  // ...保存相关的逻辑（后端的呀，或者前端的呀）
                  // 清除表格激活状态
                  this.$refs.plxTable.clearActived().then(() => {
                     // 局部保存，并将行数据恢复到初始状态（如果 第二个参数record 为空则不改动行数据，只恢复状态）
                     // 你不执行这个reloadRow，代表下次进来你上面的这个if还是会执行，不然下次点击保存，还是保存成功哦！状态没改变哦
                     this.$refs.plxTable.reloadRow(row, null, null)
                     // 如果，假如你保存了后端给你个当前行的row，那么你需要赋值给表格行呢，你就需要把后端给你的row填到第二个参数位置
                     // this.$refs.plxTable.reloadRow(row, 后端的row, null)
                     // 第三个参数呢就是只改变当前行中的call单个单元格。第三个参数是row中的字段名（field属性的值）
                  })
               //   const snapshot = JSON.parse(JSON.stringify(this.mytableData));
               //   this.history.push(snapshot);
                  this.$message({
                    message: '恭喜你，保存成功',
                    type: 'success'
                 });
                 
            } else {
               this.$message({
                  message: '保存失败，你没改变当前行的数据',
                  type: 'error'
               });
            }
          },
          // 点击编辑按钮，激活行状态-> 为编辑状态
          editRowEvent (row) {
             this.$refs.plxTable.setActiveRow(row)
          },
          // 取消
          cancelRowEvent (row) {
            // 清除表格激活状态
            this.$refs.plxTable.clearActived().then(() => {
               // 还原行数据
               this.$refs.plxTable.revertData(row)
            })
          },
          //删除选中项
          handleDelet(){
            this.$refs.plxTable.removeCheckboxRow();
            this.$message({
              type: 'success',
              message: '已删除选中项'
            });
          },
         //  数据检查弹框
         //全选
         handleCheckAllChange(val) {
            this.checkedCities = val ? cityOptions : [];
            this.isIndeterminate = false;
         },
         //单选
         handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
         },
         //获取更新后的tableData存store
         getTableData(){
            console.log(this.tableData);
            this.$store.commit("dataparse/setTableData");
         },
         // filterTableData() {
         //    const searchText = this.searchText.toLowerCase();
         //    this.mytableData = this.mytableData.filter((item) => Object.values(item).some((value) => String(value).toLowerCase().includes(searchText)));
         // },
         back(){
            this.$router.back(-1);
         },
       }
    }
  </script>
